.dashboard-container{
    display:flex;
    flex-direction:column;
    justify-content:center;
    background:#ffffff;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    
}
.dark-theme{
    background-color: #18181a;
    color:#f4f2f2;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    
}
.carousel-container{
    width:70%;
    height:400px;

}
.toggle-container{
    display:flex;
    justify-content: space-between;
    margin:10px;
}
.toggle-header{
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}


.banners{
    background-color: #d67beb;
    align-self:center;
    padding:10px;
    margin:10px;
    height:400px;
    width:100%;
    
}
.banner-img{
height:60vh;
width:100%;
background-color:#948989;
color:#fff;
}
.banner-img-container{
    color:#fff;
}

.section-container{
    margin-top:25px;
    display:flex;
    justify-content: space-around;
}
.dashboard-cards-container{
    display:flex;
    flex-wrap:wrap;
}
.section-tab-item{
    border:2px solid #a808cc;
    background-color: transparent;
    padding:5px;
    border-radius:5px;
}
@media screen and (max-width:540px){
    .section-tab-item{
        border:2px solid #a808cc;
        background-color: transparent;
        padding:2px;
        margin:3px;
        border-radius:5px;
    }

}

.section-tab-item:hover{
    background-color:#a808cc;
    border:#000;
    color:#fff;
}
.section-tab-item1{
    border:2px solid #030101;
    background-color: transparent;
    padding:5px;
    border-radius:5px;
}


.theme-toggle-button {
    border:none;
    background-color:transparent;
    
  }
  
.toggle-icon{
    font-size:35px;
    color:#fff;

}
.toggle-icon1{
    color:#000;
    font-size:35px;
}
